import React, { useEffect } from "react"
import { makeStyles } from '@material-ui/core/styles'
import Container from '@material-ui/core/Container'
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography'
import Avatar from '@material-ui/core/Avatar'

const useStyles = makeStyles({
  root: {
    position: 'relative',
    display: 'inline-block',
    height: '100vh',
    width: '500px',
    background: "white"
  },
  content: {
    marginTop: "30px",
  },
  hide: {
    display: "none",
  },
  closeButton: {
   position: 'absolute',
   right: 5,
   top: 5,
   color: "grey",
 },
 imageContainer: {
   borderRadius: "50%",
   display: "inline-block"
 },
 image: {
   width: "120px",
   height: "120px"
 }
});

const Midbar = props => {
  const classes = useStyles()

  return (
    <div className={props.showMidbar ? classes.root : classes.hide}>
      <Container className={classes.content} mt={2}>

          <div className={classes.imageContainer}>
            <Avatar src={'/therapist-images/' + props.therapist.image} className={classes.image}/>
          </div>

          <div>
            <Typography>
              {props.therapist.name}
            </Typography>
          </div>

          <IconButton aria-label="close" className={classes.closeButton} onClick={() => props.setShowMidbar(false)}>
            <CloseIcon />
          </IconButton>


      </Container>
    </div>
  )

}

export default Midbar
